<template>
  <div class="index">
    <Top gorouter="/"></Top>
    <div class="userinfo">
      <img class="userimg" src="../assets/public/user.png" alt>
      <p class="name">{{username}}</p>
      <p class="company">湖南嘉穗农业有限公司</p>
    </div>
    <div class="tasklist">
      <ul>
        <li v-for="(item , index) in tasklist" :key="item.id">
          <router-link :to="'/tasklist/'+item.status">
            <img class="abs-status" :src="item.img">
            {{item.taskname}}
            <span class="number" :class="{'text-red':index==0}">({{item.worknumber}})</span>
          </router-link>
        </li>
      </ul>
    </div>
    <div class="userout">
      <van-button type="info" size="large" class="login-button bg-blue" @click="fnout">退出</van-button>
    </div>
  </div>
</template>
<script>
import Top from "@/components/public/top";
import plusimg from "../assets/public/plus.png";
import chooseimg from "../assets/public/choose.png";
import {apiGetTaskCount} from '@/api/index';
export default {
  data() {
    return {
      username:'',
      tasklist: [
        {
          id: 1,
          taskname: "待完成的农事任务",
          status: "working",
          img: plusimg,
          worknumber: 0
        },
        {
          id: 2,
          taskname: "已完成的农事任务",
          status: "completed",
          img: chooseimg,
          worknumber: 0
        }
      ]
    };
  },
  created(){
    this.username = this.$store.state.userinfo.username;
    let id = parseInt(this.$store.state.userinfo.id);
    apiGetTaskCount(id,1).then(res=>{
      res = res.data
      if(res.code==0){
        if(res.data==null){
          return
        }
        this.tasklist[0].worknumber = res.data;
      }
    })
    apiGetTaskCount(id,3).then(res=>{
      res = res.data
      if(res.code==0){
        if(res.data==null){
          return
        }
        this.tasklist[1].worknumber = res.data;
      }
    })

  },
  methods: {
    fnout() {
      localStorage.clear('jzwl_islogin')
      this.$router.push('/login');
    }
  },
  components: {
    Top
  }
};
</script>
<style scoped>
.userinfo {
  padding: 10px 0;
  text-align: center;
  background: #0078ad;
  color: white;
}
.userinfo .userimg {
  width: 50px;
  height: 50px;
  border-radius: 50%;
}
.userinfo .name,
.userinfo .company {
  font-size: 13px;
  line-height: 20px;
  margin: 0;
}
.tasklist ul li {
  margin-bottom: 10px;
}
.tasklist ul li a {
  display: block;
  text-align: left;
  padding: 10px 25px 10px 50px;
  background: white;
  font-size: 16px;
  line-height: 40px;
  position: relative;
}
.tasklist .abs-status {
  position: absolute;
  left: 10px;
  top: 15px;
  width: auto;
  height: 30px;
}
.tasklist ul li .number {
  color: #999;
}
.tasklist,
.userout {
  padding: 20px;
}
.userout .out {
  font-size: 14px;
  background: #0078ad;
  color: white;
  line-height: 50px;
}
</style>


