import Vue from 'vue';
import Vuex from 'vuex';
Vue.use(Vuex);

const state = {
    userinfo: {},
    sum: 0,
    test: 0,
    tasklistname: ''
};
const getter = {};
const mutations = {
    // getuserinfo() {

    // },
    // addtest(state) {
    //     state.test++;
    //     console.log(state.test)
    // }
    getuserinfo: (state, data) => {
        state.userinfo = data
    },
    getTaskName: (state, id) => {
        state.tasklistid = id
    },
    adda: (state, a) => {
        return state.sum = a;
    },
    mina: state => state.sum = state.sum - 1,
    addtest: state => state.test = state.test + 1
        // adda: state => state.sum = state.sum + 1,
        // mina: state => state.sum = state.sum - 1
};
const action = {};
const module = {};
export default new Vuex.Store({
    state,
    getter,
    mutations,
    action,
    module
})


