<template>
  <div class="top">
    <van-nav-bar
      left-text="湖南机智物联"
      @click-left="onClickLeft"
      @click-right="onClickRight"
      left-arrow
    >
      <van-icon name="bars" slot="right"/>
    </van-nav-bar>
  </div>
</template>
<script>
export default {
  methods: {
    onClickLeft() {
      // console.log(123);
      // this.$toast("返回");
      this.$router.push(this.gorouter)
    },
    onClickRight() {
      this.$toast("功能暂定");
    }
  },
  props:['gorouter']
};
</script>
<style scoped>
.top .van-nav-bar__left i,.top .van-nav-bar__left span,.van-nav-bar__right i{
    color:#333;
}
</style>



