import axios from 'axios';
// const dburl = 'http://192.168.43.108:9001';
// axios.defaults.baseURL = dburl;
//响应拦截器
axios.interceptors.request.use(function(config) {
    // config.headers = {
    //         'Content-Type': 'application/json'
    //     }
    config.headers['Content-Type'] = 'application/json';

    // let token = window.localStorage.getItem('mytoken');
    // if (token) {
    // config.headers['Authorization'] = 'token';
    // }
    // -form-urlencoded
    return config;
}, function(error) {
    return Promise.reject(error);
})

//请求登录
export const apiLogin = (params) => {
        return axios.post('/wxlogin/login', params)
    }
    // 查询生产人员任务数量
export const apiGetTaskCount = (producter_id, state) => {
        return axios.get('/tTasks/producterIdCount', {
            params: {
                producter_id,
                state
            }
        })
    }
    // 查询生产人员任务
export const apiGetTaskList = (producter_id, state) => {
    return axios.get('/tTasks/producterIdList', {
        params: {
            producter_id,
            state
        }
    })
}

// 提交任务参数
export const apiGetTaskPush = (id, img1, img2, img3, message) => {
    return axios.post('/tTasks/push', {
        id,
        img1,
        img2,
        img3,
        message
    })
}


