import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/components/login'
import Index from '@/components/index'
import Tasklist from '@/components/tasklist/tasklist'
import TasklistAdd from '@/components/tasklist/tasklist_add'
Vue.use(Router)

export default new Router({
    routes: [{
        path: '/login',
        name: 'login',
        component: Login
    }, {
        path: '/index',
        name: 'index',
        component: Index,
    }, {
        path: '/tasklist/:statusname',
        name: 'tasklist',
        component: Tasklist
            // }, {
            //     path: '/tasklist_add',
            //     name: 'tasklist_add',
            //     component: TasklistAdd
    }, {
        path: '/tasklist_add/:id',
        name: 'tasklist_add',
        component: TasklistAdd,
    }, {
        path: '*',
        name: 'login',
        redirect: '/login'
    }]
})


